<!---->
<!--created by qiuwei-->
<template>
  <div class="page-detail">
    <!---->
    <div class="part-poll" @click.stop="showBigFunc(true, 0)">
      <swiper :options="poll.option" v-if="poll.data.length">
        <swiper-slide v-for="(item, index) in poll.data" :key="item">
          <div class="poll-content" @click.stop="showBigFunc(true, index)">
            <img :src="$supImg(item, '750x490')"/>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
      <!---->
      <div class="poll-time" v-if="[902, 903].indexOf(data.periodStatus) !== -1">
        <span>{{data.periodStatus === 902 ? '' : ''}}</span>
        <jd-timer :data="data.periodStatus === 902 ? (data.startTime || 0) : (data.endTime || 0)" @endBack="endBack(data)"/>
      </div>
      <!---->
      <div class="poll-win" v-if="data.periodStatus === 904">
        <span>
          <img v-lazy="{src: $supImg(data.winnerHeadImg, '40x40'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
        </span>
        {{data.winnerName || ''}} &nbsp;
      </div>
      <!---->
      <div class="poll-go" @click.stop="$router.push('/home')">
        <span></span>
      </div>
    </div>
    <!---->
    <div class="part-title" :class="{'part-title--join': data.participated}">
      <div class="title-head">{{data.goodsName || ''}}</div>
      <div class="title-bottom">
        <span class="title-img">
          <img v-lazy="{src: $supImg(data.starHeadImg, '60x60'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
        </span>
        <span class="title-name">{{data.starName || ''}}</span>
        <span class="title-line" v-if="data.starName && data.starLabel">|</span>
        <span class="title-work">{{data.starLabel || ''}}</span>
        <span
          class="title-heart"
          :class="{
            'title-heart--active': data.periodStatus === 903,
            'title-heart--end': data.periodStatus === 904
          }"
        >
          <i></i>
          <jd-integer :value="data.praiseTotalCount || 0"/>
        </span>
      </div>
    </div>
    <!---->
    <div class="part-info" :class="{show: infoShow}">
      <div class="info-panel">
        <span>{{data.goodsDesc || ''}}</span>
        <span class="toggleBtn" @click="infoShow=!infoShow" v-if="data.goodsDesc && data.goodsDesc.length > 50"><i></i></span>
      </div>
    </div>
    <!---->
    <div class="part-flow">
      <p class="flow-title">
        <span></span>
      </p>
      <ul class="flow-img">
        <li><div></div></li>
        <li><div></div></li>
        <li><div></div></li>
        <li><div><br/></div></li>
      </ul>
    </div>
    <!---->
    <div class="bottomBtn" :class="{isDisable: data.periodStatus !== 903}" @click="shareBtn" v-if="[902, 903, 904].indexOf(data.periodStatus) !== -1">
      <span>{{shareBtnText}}</span>
      <i></i>
    </div>
    <!---->
    <jd-popup :visible.sync="showBig" @click.native="showBigFunc(false)" class="pollBig">
      <swiper :options="pollBig.option" v-if="pollBig.data.length > 0" ref="mySwiper">
        <swiper-slide v-for="item in pollBig.data" :key="item.id">
          <div class="pollBig-content">
            <img v-lazy="{src: $supImg(item, '900x900', 'n1', false)}"/>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </jd-popup>
  </div>
</template>

<script>
//     app 
import { cornerShare, bottomShare, getDeviceType, pageWxShare, logSend, getUrlParam } from '../../assets/js/util'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'page-detail',
  components: { swiper, swiperSlide },
  data () {
    return {
      // 
      poll: {
        data: [],
        option: {
          speed: 800,
          autoplay: { delay: 3000, disableOnInteraction: false },
          loop: true,
          pagination: { el: '.swiper-pagination' }
        }
      },
      // 
      infoShow: false,
      // 
      showBig: false,
      pollBig: {
        data: [],
        option: {
          loop: false,
          pagination: { el: '.swiper-pagination', type: 'fraction' }
        }
      }
    }
  },
  computed: {
    ...mapState('common', [
      'jdInfo' // 
    ]),
    ...mapGetters('detail', [
      'data' // 
    ]),
    // 
    shareBtnText () {
      switch (this.data.periodStatus) {
        case 902: return ''
        case 903: return '  '
        case 904: return ''
        default: return ''
      }
    }
  },
  created () {
    document.body.style.background = '#fff'
    document.body.scrollTop = 0
    // pv
    setTimeout(() => {
      logSend({
        page_title: '',
        activityId: getUrlParam('periodCode')
      })
    }, 500)
  },
  async mounted () {
    // 
    await this.queryDetail()
    //  
    if (sessionStorage.getItem(`want_${this.data.periodCode}`)) {
      sessionStorage.removeItem(`want_${this.data.periodCode}`)
      setTimeout(() => { this.shareBtn() }, 0)
      return
    }
    let imgUrl = location.protocol + this.$supImg(this.poll.data[0], '90x90')
    // App
    cornerShare(imgUrl)
    // 
    pageWxShare({pic: imgUrl})
  },
  methods: {
    ...mapActions('detail', [
      'clearDetail', // 
      'getDetail', // 
      'joinAct' // 
    ]),
    ...mapActions('common', [
      'judgeJdIn' // 
    ]),
    // 
    async queryDetail () {
      await this.getDetail()
      // 
      this.poll.data = this.pollBig.data = this.data.detailImgs || []
    },
    // 
    async shareBtn () {
      let type = getDeviceType()
      // 
      if (this.data.periodStatus !== 903) {
        return
      }
      // 
      if (!await this.judgeJdIn()) {
        if (type === 'wx') { //    shareBtn
          sessionStorage.setItem(`want_${this.data.periodCode}`, 1)
        }
        return
      }
      // 
      if (!this.data.participated) {
        if (!await this.joinAct()) {
          return
        } else {
          //  
          logSend({
            eventId: 'paipai_1536564000120|3',
            loginPin: this.jdInfo,
            activityId: this.data.periodCode
          }, 'click')
        }
      }

      let encodeJdInfo = this.jdInfo.indexOf('%') === -1 ? encodeURIComponent(this.jdInfo) : this.jdInfo
      let shareUrl = `/share?activityInfoId=${this.data.periodCode}&joinPin=${encodeJdInfo}`
      let imgUrl = `${location.protocol}//img20.360buyimg.com/n1/s215x160_${(this.data.detailImgs || [])[0]}`
      if (type === 'paipaiApp' || type === 'app') {
        // APP
        bottomShare('/c2c/star' + shareUrl, imgUrl)
      } else {
        // 
        location.href = `${location.protocol}//${location.host}/c2c/star${shareUrl}&from=detail`
        // this.$router.push(`${shareUrl}&from=detail`)
      }
    },
    // 
    showBigFunc (show, index) {
      if (show && this.$refs.mySwiper) {
        this.$refs.mySwiper.swiper.slideTo(index, 0, false)
      }
      this.showBig = show
    },
    // 
    endBack (data) {
      data.periodStatus++
    }
  },
  destroyed () {
    // 
    this.clearDetail()
  }
}
</script>

